import React from "react";
import styled from "styled-components";
import { AnimationOnScroll } from "react-animation-on-scroll";
import "animate.css/animate.min.css";
import me from "../../assets/imgs/introduce/me.png";

function Introduce() {
  return (
    <Wrapper>
      <Inner>
        <AnimationOnScroll
          initiallyVisible={true}
          animateIn="animate__bounce"
          delay={10}
        >
          <SectionName> Introduce</SectionName>
        </AnimationOnScroll>
        <AnimationOnScroll
          initiallyVisible={true}
          animateIn="animate__fadeInUp"
          delay={300}
        >
          <Contents>
            <ImgArea>
              <img src={me} alt="psw" />
            </ImgArea>
            <TextArea>
              <Title>
                !
                <br />   . 
              </Title>
              <Paragraph>
                 5      .
                <br />
                <br />
                    ,
                <br />
                      .
                <br />
                <br />
                React ES6  , Typescript  ,
                <br />
                    .
                <br />
                <br />
                      !
                <br />
                 :)
              </Paragraph>
            </TextArea>
          </Contents>
        </AnimationOnScroll>
      </Inner>
    </Wrapper>
  );
}

export default Introduce;

const Wrapper = styled.section`
  position: relative;
  padding: 8rem 10rem 2rem;
  height: 100vh;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 180px;
    background: linear-gradient(
      to top,
      ${(props) => props.theme.bgRGBAColor},
      ${(props) => props.theme.bgGradientEndColor}
    );
  }
`;

const Inner = styled.div`
  max-width: 1680px;
  margin: 0 auto;
`;

const SectionName = styled.div`
  font-size: 7.2rem;
  font-weight: 700;
`;

const Contents = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 10rem;
`;

const Area = styled.div`
  width: 50%;
  padding: 1rem;
`;

const ImgArea = styled(Area)`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const TextArea = styled(Area)``;

const Title = styled.h2`
  font-size: 4.8rem;
  font-weight: 700;
`;

const Paragraph = styled.p`
  padding-top: 1.2rem;
  font-size: 2.4rem;
  font-weight: 400;
`;
