import React from "react";
import styled from "styled-components";
import { AnimationOnScroll } from "react-animation-on-scroll";
import "animate.css/animate.min.css";
import { ReactComponent as html5 } from "../../assets/imgs/svgIcon/html5.svg";
import { ReactComponent as css3 } from "../../assets/imgs/svgIcon/css3.svg";
import { ReactComponent as javascript } from "../../assets/imgs/svgIcon/javascript.svg";
import { ReactComponent as react } from "../../assets/imgs/svgIcon/react.svg";
import { ReactComponent as typescript } from "../../assets/imgs/svgIcon/typescript.svg";
import { ReactComponent as jquery } from "../../assets/imgs/svgIcon/jquery.svg";
import { ReactComponent as java } from "../../assets/imgs/svgIcon/java.svg";
import { ReactComponent as rdb } from "../../assets/imgs/svgIcon/amazondynamodb.svg";
import { ReactComponent as apachetomcat } from "../../assets/imgs/svgIcon/apachetomcat.svg";
import { ReactComponent as git } from "../../assets/imgs/svgIcon/git.svg";
import { ReactComponent as docker } from "../../assets/imgs/svgIcon/docker.svg";

const skills = [
  {
    name: "HTML5",
    icon: html5,
    iconColor: "#E34F26",
    content: "      .",
  },
  {
    name: "CSS3",
    icon: css3,
    iconColor: "#1572B6",
    content: "      .",
  },
  {
    name: "Javascript",
    icon: javascript,
    iconColor: "#F7DF1E",
    content:
      "            .",
  },
  {
    name: "React",
    icon: react,
    iconColor: "#61DAFB",
    content:
      "          state    styled, framer-motion, react-hook-form, router-dom, recoil, react-query       .",
  },
  {
    name: "Typescript",
    icon: typescript,
    iconColor: "#3178C6",
    content:
      "Typescript React  ,   javascript            .",
  },
  {
    name: "jQuery",
    icon: jquery,
    iconColor: "#0769AD",
    content:
      "jQuery             ,       jQuery     .",
  },
  {
    name: "JAVA",
    icon: java,
    iconColor: "#e74c3c",
    content:
      "        api,  ,     .",
  },
  {
    name: "RDB",
    icon: rdb,
    iconColor: "#4053D6",
    content:
      "Oracle, Mysql, Mssql, MariaDb, Postgresql  rdb    crud .",
  },
  {
    name: "Apache Tomcat",
    icon: apachetomcat,
    iconColor: "#F8DC75",
    content:
      "   was       https,     tomcat    .",
  },
  {
    name: "Git",
    icon: git,
    iconColor: "#F05032",
    content:
      "      branch   merge  ,   revert  . github      commit     .()",
  },
  {
    name: "Docker",
    icon: docker,
    iconColor: "#2496ED",
    content:
      "         ,           .",
  },
];

function zeroTen(num: number): string {
  return num < 10 ? `0${num}` : num + "";
}

function Skills() {
  return (
    <Wrapper>
      <Inner>
        <AnimationOnScroll
          initiallyVisible={true}
          animateIn="animate__bounce"
          delay={10}
        >
          <Title> Skills</Title>
        </AnimationOnScroll>
        <AnimationOnScroll
          initiallyVisible={true}
          animateIn="animate__fadeInUp"
          delay={300}
        >
          <Area>
            <Contents>
              {skills &&
                skills.map((skill, idx) => (
                  <Item key={`skill${idx}`}>
                    <Number>{zeroTen(idx + 1)} /</Number>
                    <Div>
                      <Icon>
                        <skill.icon
                          width={40}
                          height={40}
                          fill={skill.iconColor}
                        />
                      </Icon>
                      <FlexColumn>
                        <Name>{skill.name}</Name>
                        <Content>{skill.content}</Content>
                      </FlexColumn>
                    </Div>
                  </Item>
                ))}
            </Contents>
          </Area>
        </AnimationOnScroll>
      </Inner>
    </Wrapper>
  );
}

export default Skills;

const Wrapper = styled.section`
  padding: 2rem 10rem;
`;

const Inner = styled.div`
  max-width: 1680px;
  margin: 0 auto;
`;

const Title = styled.div`
  font-size: 7.2rem;
  font-weight: 700;
`;

const Area = styled.div`
  padding: 10rem;
`;

const Contents = styled.ul`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8rem 1rem;
`;

const Item = styled.li``;

const Icon = styled.div`
  width: 5.7rem;
  height: 5.7rem;
  svg {
    margin-top: 0.4rem;
  }
`;
const Number = styled.span`
  font-size: 3rem;
  font-weight: 700;
`;
const Div = styled.div`
  display: flex;
  margin-top: 0.4rem;
`;

const FlexColumn = styled.div`
  width: calc(100% - 7.7rem);
  display: flex;
  flex-direction: column;
`;
const Name = styled.span`
  padding: 0 1rem;
  font-size: 1.8rem;
  font-weight: 500;
`;
const Content = styled.span`
  padding: 0 1rem;
  font-size: 1.4rem;
`;
