const WORKS_LIST = [
  {
    fullTitle: "  (C-TAS) ",
    background: c_tas,
    title: "C-TAS",
    company: " & KISA",
    period: "22 3 - 22 8",
    worksType: ["SI", "SM"],
    type: ["PC"],
    comments:
      "    SI, SM  . 22           (Mybatis injection, XSS)      ,  jsp                . tiles AOP         .",
    link: "https://cshare.krcert.or.kr:8443/index",
  },
  {
    fullTitle: "     ",
    background: yangsansi,
    title: " ",
    company: " & ",
    period: "21 6 - 21 12",
    worksType: ["SI", "SM"],
    type: ["PC"],
    comments:
      "       UI       ,   .               .",
    link: "http://data.yangsan.go.kr/",
  },
  {
    fullTitle: "    ",
    background: kcert,
    title: "  ",
    company: " & KISA",
    period: "21 6 - 21 12",
    worksType: ["SI", "SM"],
    type: ["PC", "MOBILE"],
    comments:
      " 21        .   (NVD, , )    ElastickSearch     . MongoDB, ElasticSearch   Monstache    .    ES6           , Babel  ES6  , IE     .             ,   .",
    link: "https://knvd.krcert.or.kr/",
  },
  {
    fullTitle: "   ",
    background: check_kisa,
    title: "   ",
    company: " & KISA",
    period: "19 6 - 21 1",
    worksType: ["SI", "SM"],
    type: ["PC", "MOBILE"],
    comments:
      "    SI, SM  . 19, 20   2         ,   ,     .       ,   .        ,   3D    , Three.js    UI .",
    link: "https://check.kisa.or.kr/kisa/",
  },
  {
    fullTitle: "IPCR",
    background: ipcr,
    title: "IPCR",
    company: " & Ericsson-LG",
    period: "18 1 - 18 12",
    worksType: ["SI", "SM"],
    type: ["PC"],
    comments:
      "  Furence       .        ,       .  PHP   JAVA  ,  ()   C        .                .",
    link: "https://ipecs.co.kr/",
  },
  {
    fullTitle: "RECSEE",
    background: recsee,
    title: "RecSee",
    company: "",
    period: "18 1 ~ 18 12",
    worksType: ["DEV", "SM"],
    type: ["PC"],
    comments:
      "      ,       .   php  java            .     SI     ,  ,  , LG U+ .                        .",
    link: "http://www.recsee.net/",
  },
];

function Works() {
  const [isHover, setIsHover] = useState([
    false,
    false,
    false,
    false,
    false,
    false,
  ]);

  const toggleHover = (mode: string, idx: number) => {
    setIsHover((prev) => {
      const copyArr = [...prev];
      const frontArr = copyArr.slice(0, idx);
      const endArr = copyArr.slice(idx + 1, copyArr.length);
      if (mode === "enter") {
        return [...frontArr, true, ...endArr];
      } else {
        return [...frontArr, false, ...endArr];
      }
    });
  };

  return (
    <Wrapper>
      <Inner>
        <AnimationOnScroll
          initiallyVisible={true}
          animateIn="animate__bounce"
          delay={10}
        >
          <SectionName> Works</SectionName>
        </AnimationOnScroll>
        <AnimationOnScroll
          initiallyVisible={true}
          animateIn="animate__fadeInUp"
          delay={300}
        >
          <Area>
            <Contents>
              {WORKS_LIST.map((work, idx) => (
                <Article
                  key={`works${idx}`}
                  onMouseEnter={() => toggleHover("enter", idx)}
                  onMouseLeave={() => toggleHover("leave", idx)}
                >
                  <Content>
                    <img src={work.background} alt={work.fullTitle} />
                    <FadeIn className={isHover[idx] ? "fadeIn" : "hover"}>
                      <TypeInfo>
                        <Sep>WEB /</Sep>
                        <Sep>
                          {work.worksType.map((wt, idx) =>
                            idx + 1 === work.worksType.length ? wt : wt + "&"
                          )}
                        </Sep>
                        <Sep>
                          {"/ "}
                          {work.type.map((t, idx) =>
                            idx + 1 === work.type.length ? t : t + "&"
                          )}
                        </Sep>
                      </TypeInfo>
                      <SepLine />
                      <Title>{work.title}</Title>
                      <Company>{work.company}</Company>
                      <Period>{work.period}</Period>
                      <Comments>{work.comments}</Comments>
                      <GoLink href={work.link} target="_blank">
                        
                      </GoLink>
                    </FadeIn>
                  </Content>
                </Article>
              ))}
            </Contents>
          </Area>
        </AnimationOnScroll>
      </Inner>
    </Wrapper>
  );
}

export default Works;

const Wrapper = styled.section`
  padding: 2rem 10rem;
  height: 100%;
`;

const Inner = styled.div`
  max-width: 1680px;
  margin: 0 auto;
`;

const SectionName = styled.div`
  font-size: 7.2rem;
  font-weight: 700;
`;

const Area = styled.div`
  width: 100%;
  padding: 10rem;
`;

const Contents = styled.ul`
  display: flex;
  flex-wrap: wrap;
`;

const CursorPointer = styled.li`
  cursor: pointer;
`;

const Article = styled(CursorPointer)`
  float: left;
  width: 33.3%;
`;

const Content = styled.div`
  position: relative;
  width: 100%;

  img {
    display: block;
    width: 100%;
    height: 100%;
  }
`;

const FadeIn = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: all 0.4s;

  &.hover {
    opacity: 0;
  }
  &.fadeIn {
    padding: 2rem;
    opacity: 1;
    color: ${(props) => props.theme.bgColor};
    background: rgba(87, 96, 111, 0.8);
  }
`;

const Title = styled.div`
  font-size: 3.2rem;
  font-weight: 500;
`;
const TypeInfo = styled.div`
  font-size: 2rem;
  font-weight: 400;

  span ~ span {
    margin-left: 1rem;
  }
`;
const Sep = styled.span`
  font-size: 1.4rem;
`;
const SepLine = styled.div`
  width: 3rem;
  border: 1px solid ${(props) => props.theme.bgColor};
`;
const Company = styled.div`
  font-size: 1.6rem;
  font-weight: 400;
`;
const Period = styled.div`
  font-size: 1.4rem;
  font-weight: 400;
`;
const Comments = styled.span`
  display: block;
  margin: 1rem 0;
  font-size: 1.2rem;
`;

const GoLink = styled.a`
  font-style: italic;
  font-size: 1.4rem;
  text-decoration: underline;
`;
